      </el-main>
    </el-container>
    <el-footer>
      <div style="">
          <div style="width:50%;margin:0 auto;display:flex;">
            <audio  ref="audio" controls style="width:80%" >
                 audio 
            </audio>
            <div>
                <div class="my-font" style="font-size:20px;font-bold"></div>
                <div></div>
            </div>
            <div>
                
            </div>
          </div>


      </div>
      
    </el-footer>
  </div>
</template>

<script>
import Bar from "./components/Header";
import Menu from "./components/Menu";

import DicoverMusic from "./views/discoverMusic/Discover";

export default {
  name: "App",
  components: {
    Bar,
    Menu,
    DicoverMusic

  },
  data(){
    return{
      audio:null
    }
  },
  created() {
    this.$message("");
  },

  methods:{
    play(e){
      // console.log('play')
      this.$refs.audio.src = e
      this.$refs.audio.play()
    }
  }
  
};
</script>

<style  scoped>
body {
